import { Button } from "../styled";

export const Error = ({ oldData }) => {
  return (
    <>
      {oldData ?
        <>
          <p>Wystpi bd przy pobieraniu danych.</p>
          <p>Czy chcesz uy kopi z&nbsp;
            {new Date(oldData.date).toLocaleDateString()}r&nbsp;?
          </p>
          <Button> OK </Button>
        </>
        :
        <>
          <p>Niestety wystpi bd pobierania danych.</p>
          <p>Sprbuj pniej.</p>
        </>
      }
    </>
  )
};